<template>
  <div class="hot">
    <!-- <search-list></search-list>-->
    <!-- <router-link tag="section" to="/search">
      <search-list></search-list>
    </router-link> -->
    <div class="hottop"></div>
    <div class="top" v-if="listInfo">
      <div class="trtop"></div>
      <div class="twtop">
        更新时间:{{ this.listInfo.trackUpdateTime | formatDate("m月d日") }}
      </div>
    </div>
    <!-- <border-title>热门歌手</border-title>
    <div class="lists">
      <linger-list
        v-for="lists in linger_list"
        :key="lists.id"
        :item="lists"
      ></linger-list>
    </div> -->
    <newest-music
      v-for="(newest, index) in hotList"
      :key="newest.id"
      :item="newest"
      @play="$emit('play', $event)"
      :musicID="musicID"
    >
      <template v-slot:sortnum>
        <span
          class="spanul"
          >{{(index+1) &lt; 10 ? '0' +(index+1) : (index+1)}}</span
        >
      </template>
    </newest-music>
    <loading v-if="isLoading || hotList.length != allList.length" />
    <p class="loadingert" v-show="allList.length > 0">
      {{ hotList.length == allList.length ? "已经没有更多啦~" : "加载中..." }}
    </p>
    <div class="musicbottom"></div>
  </div>
</template>

<script>
// import BorderTitle from "@/components/BorderTitle";
// import LingerList from "@/components/LingerList";
import Loading from "@/components/Loading";
// import SearchList from "@/components/SearchList";
import NewestMusic from "@/components/NewestMusic";
export default {
  props: ["musicID"],
  name: "Hot",
  components: {
    // BorderTitle,
    // LingerList,
    // SearchList,
    NewestMusic,
    Loading,
  },
  data() {
    return {
      linger_list: [],
      listInfo: null,
      //  热歌榜歌曲显示的数组
      hotList: [],
      //  热歌榜所有歌曲的数组
      allList: [],
      //  页码
      page: 0,
      // 每页加载的歌曲数目20
      perpage: 20,
      // 节奏条的显示隐藏
      isLoading: true,
      // 定时器
      timer: null,
    };
  },
  methods: {
    // 加载更多歌曲
    loadMore() {
      // 加载数据时显示
      this.isLoading = true;
      // 延迟0.8秒执行加载数据
      clearInterval(this.timer);
      this.timer = setTimeout(() => {
        this.page++;
        // 方法1:
        let start = this.page * this.perpage;
        let end = (this.page + 1) * this.perpage;
        this.hotList = this.hotList.concat(this.allList.slice(start, end));
        // 方法2:
        // this.hotList = this.allList.slice(0,end);
        // 数据加载完就隐藏
      }, 800);
      this.isLoading = false;
    },
    // 滚轮事件
    onScroll() {
      let scrollHeight = document.documentElement.scrollHeight;
      let scrollTop = document.documentElement.scrollTop;
      let clientHeight = document.documentElement.clientHeight;
      if (scrollHeight - (scrollTop + clientHeight) - 50 <= 0) {
        this.loadMore();
      }
    },
  },

  created() {
    this.axios.get("/artist/list?cat=5001").then((data) => {
      this.linger_list = data.data.artists.slice(0, 6);
      console.log(this.linger_list);
    });
    this.axios.get("/playlist/detail?id=3778678").then((d) => {
      console.log(d.data.playlist);
      this.listInfo = d.data.playlist;
      this.allList = d.data.playlist.tracks.map((data, index) => {
        data.song = {
          bMusic: {
            sr: d.data.privileges[index].maxbr,
          },
          artists: data.ar,
          album: data.al,
          alias: [],
        };
        data.picUrl = data.al.picUrl;
        return data;
      });
      this.hotList = this.allList.slice(0, 20);

      // 添加滚轮监听器
      window.addEventListener("scroll", this.onScroll);
    });
  },
  //离开路由要移除滚动事件
  beforeRouteLeave(to, from, next) {
    //移除滚动.
    window.removeEventListener("scroll", this.onScroll);
    next();
  },
  //时间过滤器
  filters: {
    formatDate(value, fstr) {
      let d = new Date(value);
      let formatD = {
        "m+": d.getMonth(),
        "d+": d.getDate(),
      };
      for (let i in formatD) {
        let reg = new RegExp(i);
        let val = formatD[i];
        val = val < 10 ? "0" + val : val;
        fstr = fstr.replace(reg, val);
      }
      return fstr;
    },
  },
  // beforeRouteEnter(to, from, next) {
  //   next((vm) => {
  //     vm.axios
  //       .get("/playlist/detail?id=" + vm.$route.params.usernameid)
  //       .then((d) => {
  //         vm.listInfo = d.data.playlist;
  //         console.log(vm.listInfo);
  //         vm.allList = d.data.playlist.tracks.map((data, index) => {
  //           data.song = {
  //             bMusic: {
  //               sr: d.data.privileges[index].maxbr,
  //             },
  //             artists: data.ar,
  //             album: data.al,
  //             alias: [],
  //           };
  //           return data;
  //         });
  //         vm.hotList = vm.allList.slice(0, vm.page);
  //         console.log(vm.hotList);
  //         console.log();
  //       });
  //   });
  // },
};
</script>
<style lang="less" scoped>
.hot {
  .newests {
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5) {
      color: #df3436;
    }
  }
  .top {
    width: 100%;
    height: 45vw;
    background: url(../assets/hot_music_bg_2x.jpg);
    display: flex;
    flex-direction: column;
    justify-content: center;
    img {
      width: 100%;
    }
    .trtop {
      width: 142px;
      height: 67px;
      background-position: -24px -30px;
      background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=)
        no-repeat 100% 100%;
      background-size: 166px 97px;
    }
    .twtop {
      color: #fff;
    }
  }
  // .hottop {
  //   height: 47px;
  // }
  .lists {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    div {
      width: 32%;
    }
  }
  .navbarsTop {
    height: 30px;
  }
  .navbars {
    display: flex;
    line-height: 45px;
    width: 100%;
    background-color: #fff;
    li {
      flex: 1;
      text-align: center;
      a {
        display: inline-block;
        font-size: 15px;
        color: #333;
        &.router-link-exact-active {
          color: #dd001b;
          position: relative;
          &::after {
            content: "";
            display: block;
            width: calc(~"100% + 20px");
            margin-left: -10px;
            border: none;
            border-bottom: 2px solid #dd001b;
            position: absolute;
            bottom: -1px;
          }
        }
      }
    }
    border-bottom: 1px solid #ccc;
  }
  .loadingert {
    text-align: center;
  }
  .musicbottom {
    width: 100%;
    height: 12vw;
  }
}
</style>